.button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 13px 45px;
    border-radius: 100px;
    font-family: var(--font-georgia);
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 130%;
    letter-spacing: 0.06em;
    text-decoration: none;
    color: var(--color-dark-3xl);
    box-sizing: border-box;
    transition: background-color 0.3s ease-in-out;    
}

.button--primary {

    background-color: var(--color-primary);
    border: 2px solid var(--color-primary);

    &:hover,
    &:focus {
        @include card-hover;
    }
}

.button--light {
    background-color: var(--color-light-xl);
    border: 2px solid var(--color-primary);

    &:hover,
    &:focus {
        @include card-hover;        
    }
}

.button-circle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 52px;
    height: 52px;
    text-decoration: none;
    background-color: transparent;
    border: 2px solid var(--color-primary);
    border-radius: 50%;
    box-sizing: border-box;
    transition: background-color 0.3s ease-in-out;

    &:hover,
    &:focus, 
    .overlay:hover ~ .popup &,
    .overlay:active ~ .popup & {
        background-color: var(--color-primary);
        cursor: pointer;
    }

    &:disabled,
    &[data-disabled="true"] {
        color: var(--color-dark-s);
        border-color: var(--color-dark-s);

        svg {
            color: var(--color-dark-s);
        }

        &:hover,
        &:focus {
            background-color: transparent;
            cursor: auto;
        }
    }

    svg {
        display: block;
    }
}


.button-circle--active {
    background-color: var(--color-primary);    
}
