:root {
  --font-georgia: "Georgia";
  --font-arial: "Arial";
  --color-light-xl: #ffffff;
  --color-light-l: #F6F6F6;
  --color-light-s: #FAFAFA;
  --color-dark-s: #CDCDCD;
  --color-dark-m: #B2B2B2;
  --color-dark-l: #545454;
  --color-dark-xl: #4C4C4C;
  --color-dark-2xl: #444444;
  --color-dark-3xl: #292929;
  --color-primary: #F1CDB3;
  --color-primary-light: #FDDCC4;
  --start-screen-gradient-background: url(../img/noise_lines_transparent@2X.png),
      radial-gradient(100% 215.42% at 0% 0%, #5B483A 0%, #262425 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */,
      #211F20;
  --footer-gradient-background: url(../img/noise_lines_transparent@2X.png), radial-gradient(110.67% 538.64% at 5.73% 50%, #513D2F 0%, #1A1A1C 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */, #211F20;
  --padding: 40px;
  --padding-top: 80px;
  --padding-bottom: 100px;
  --margin-standart: 25px;
}
@media (max-width: 1279px) {
  :root {
    --padding: 30px;
  }
}
@media (max-width: 767px) {
  :root {
    --padding: 10px;
  }
}

