html {
  min-height: 100vh;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

input[type=number] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

@font-face {
  font-display: swap;
  font-family: "Arial";
  src: local("ArialMT"), url("../fonts/ArialMT.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Arial";
  src: local("Arial-BoldMT"), url("../fonts/Arial-BoldMT.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Georgia";
  src: local("Georgia"), url("../fonts/Georgia.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Georgia";
  src: local("Georgia-Bold"), url("../fonts/Georgia-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-size: 15px;
  line-height: 160%;
  font-family: var(--font-arial), "Arial", sans-serif;
  font-weight: 400;
  color: var(--color-dark-xl);
  font-style: normal;
  background: var(--color-light-xl);
}

main {
  flex-grow: 1;
  background-color: var(--white);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.body {
  position: relative;
}

.table-responsive {
  overflow: hidden;
  overflow-x: auto;
}

.container {
  padding-left: var(--padding);
  padding-right: var(--padding);
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
}

.overflow-hidden {
  max-height: 100vh;
  overflow: hidden;
}

@keyframes move-right {
  from {
    left: 0px;
  }
  to {
    left: -1080px;
  }
}
@keyframes move-left {
  from {
    left: -1080px;
  }
  to {
    left: 0;
  }
}
@keyframes move-top {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(0, -900px, 0);
  }
}
@media (max-width: 1279px) {
  @keyframes move-right {
    from {
      left: 0px;
    }
    to {
      left: -620px;
    }
  }
  @keyframes move-left {
    from {
      left: -620px;
    }
    to {
      left: 0;
    }
  }
  @keyframes move-top {
    from {
      transform: translate3d(0, 0, 0);
    }
    to {
      transform: translate3d(0, -1360px, 0);
    }
  }
}
@media (max-width: 767px) {
  @keyframes move-right {
    from {
      left: 0px;
    }
    to {
      left: -310px;
    }
  }
  @keyframes move-left {
    from {
      left: -310px;
    }
    to {
      left: 0;
    }
  }
  @keyframes move-top {
    from {
      transform: translate3d(0, 0, 0);
    }
    to {
      transform: translate3d(0, -1360px, 0);
    }
  }
}
.transition-right {
  animation-name: move-right;
  animation-duration: 3s;
}

.transition-left {
  animation-name: move-left;
  animation-duration: 3s;
}

.transition-top {
  animation-name: move-top;
  animation-duration: 3s;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 13px 45px;
  border-radius: 100px;
  font-family: var(--font-georgia);
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 130%;
  letter-spacing: 0.06em;
  text-decoration: none;
  color: var(--color-dark-3xl);
  box-sizing: border-box;
  transition: background-color 0.3s ease-in-out;
}

.button--primary {
  background-color: var(--color-primary);
  border: 2px solid var(--color-primary);
}
.button--primary:hover, .button--primary:focus {
  background: var(--color-primary-light);
  cursor: pointer;
}

.button--light {
  background-color: var(--color-light-xl);
  border: 2px solid var(--color-primary);
}
.button--light:hover, .button--light:focus {
  background: var(--color-primary-light);
  cursor: pointer;
}

.button-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 52px;
  height: 52px;
  text-decoration: none;
  background-color: transparent;
  border: 2px solid var(--color-primary);
  border-radius: 50%;
  box-sizing: border-box;
  transition: background-color 0.3s ease-in-out;
}
.button-circle:hover, .button-circle:focus, .overlay:hover ~ .popup .button-circle, .overlay:active ~ .popup .button-circle {
  background-color: var(--color-primary);
  cursor: pointer;
}
.button-circle:disabled, .button-circle[data-disabled=true] {
  color: var(--color-dark-s);
  border-color: var(--color-dark-s);
}
.button-circle:disabled svg, .button-circle[data-disabled=true] svg {
  color: var(--color-dark-s);
}
.button-circle:disabled:hover, .button-circle:disabled:focus, .button-circle[data-disabled=true]:hover, .button-circle[data-disabled=true]:focus {
  background-color: transparent;
  cursor: auto;
}
.button-circle svg {
  display: block;
}

.button-circle--active {
  background-color: var(--color-primary);
}

.header {
  padding-top: 30px;
  padding-bottom: 30px;
}

.header--main {
  position: absolute;
  top: 30px;
  left: 0;
  width: 100%;
  background-color: transparent;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header--inside {
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: var(--color-light-xl);
}

.header--inside .header__logo {
  color: var(--color-dark-l);
}

.header__toggler {
  display: none;
}

@media (max-width: 767px) {
  .header.opened {
    z-index: 3;
    padding-top: 0;
    padding-bottom: 0;
    margin-bottom: 118px;
  }

  .header__menu {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 100%;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--color-dark-s);
    width: 320px;
    background-color: var(--color-dark-3xl);
    transition: right 0.3s ease-in-out;
  }
  .header.opened .header__menu {
    right: 0;
  }

  .header__toggler {
    display: block;
  }

  .header__wrapper {
    padding-right: 53px;
  }

  .header__toggler {
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0;
    padding: 0;
    width: 30px;
    height: 22px;
    background-color: transparent;
    border: 0;
    transition: transform 0.3s ease-in-out;
  }
  .header__toggler span {
    display: block;
    width: 30px;
    height: 2px;
    background-color: var(--color-primary);
  }
  .header.opened .header__toggler {
    position: fixed;
    right: 53px;
    top: 50px;
    transform: rotate(90deg);
  }

  .header--inside .header__toggler span {
    background-color: #000000;
  }

  .header--inside.opened .header__toggler span {
    background-color: var(--color-primary);
  }

  .header--inside.opened .logo__name {
    color: var(--color-primary);
  }

  .header--inside.opened .logo__subname {
    color: var(--color-light-xl);
  }

  .header__logo {
    right: 0;
    transition: right 0.3s ease-in-out;
  }
  .header.opened .header__logo {
    position: fixed;
    right: 106px;
    top: 30px;
    z-index: 2;
    transition: right 0.3s ease-in-out;
  }
}
.menu__list {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  grid-gap: 35px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.menu__link {
  position: relative;
  display: flex;
  padding-bottom: 3px;
  font-size: 15px;
  line-height: 160%;
  color: var(--color-dark-s);
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}
.menu__link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  width: 0;
  height: 3px;
  background-color: transparent;
}
.menu__link:hover, .menu__link:focus {
  color: var(--color-light-s);
  cursor: pointer;
}
.header--inside .menu__link {
  color: var(--color-dark-l);
}
.header--inside .menu__link:hover, .header--inside .menu__link:focus {
  color: var(--color-dark-3xl);
}

.menu__link--active::after {
  left: 0;
  width: 100%;
  background-color: var(--color-primary);
}
.header--inside .menu__link--active::after {
  color: var(--color-dark-l);
}

@media (max-width: 1279px) {
  .header--main {
    top: 0;
  }
}
@media (max-width: 767px) {
  .menu__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 40px;
    padding-top: 70px;
  }

  .menu__link {
    font-size: 32px;
    line-height: 160%;
  }
  .header--inside.opened .menu__link {
    color: var(--color-dark-s);
  }
  .header--inside.opened .menu__link:hover, .header--inside.opened .menu__link:focus {
    color: var(--color-light-s);
  }

  .header--inside.opened .menu__link--active::after {
    color: var(--color-primary);
  }
}
